<template>
    <section>
        <router-link v-if="firstID==='all'" :to="{ name: '实验组列表', params: {project_id: this.$route.params.project_id}}" style='text-decoration: none;color: aliceblue;'>
            <el-button class="return-list"><i class="el-icon-d-arrow-left" style="margin-right: 5px"></i>实验组列表</el-button>
        </router-link>
        <router-link v-if="firstID!='all'" :to="{ name: '分组实验列表', params: {project_id: this.$route.params.project_id, firstGroup:firstID}}" style='text-decoration: none;color: aliceblue;'>
            <el-button class="return-list"><i class="el-icon-d-arrow-left" style="margin-right: 5px"></i>实验组列表</el-button>
        </router-link>
        <router-link :to="{ name: '创建实验服务'}" style='text-decoration: none;color: #000000;'>
            <el-button type="primary" style="margin-left:10px"><i class="el-icon-plus" style="margin-right: 5px;margin-left:10px"></i>新建实验实例</el-button>
        </router-link>

        <el-table :data="ApiList" highlight-current-row
                  v-loading="listLoading"
                  @expand-change="expandExpClick"
                  style="width: 100%;">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <small><b>实验描述:</b>
                        {{props.row.description}}
                    </small>
                    <br>
                    <small><b>优化目标:&nbsp;</b> <el-tag type="danger" size="mini">clicks</el-tag></small>
                    <br>
                    <br>
                    <el-table :data="altListData" highlight-current-row style="width: 100%;">
                        <el-table-column prop="name" label="候选实例名称" min-width="30%">
                        </el-table-column>
                        <el-table-column prop="exposure_ratio" label="曝光率(%)" min-width="30%">
                        </el-table-column>
                         <el-table-column prop="model_name" label="使用模型" min-width="40%">
                        </el-table-column>
                    </el-table>
                </template>
            </el-table-column>

            <el-table-column type="index" width="55">
            </el-table-column>
            <el-table-column prop="name" label="实验详情名称" min-width="15%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-icon name="name"></el-icon>
                    <router-link :to="{ name: '查看实验详情', params: {api_id: scope.row.id}}" style='text-decoration: none;color: #000000;'>{{ scope.row.name }}
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="altlist" label="实验类型" min-width="25%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-tag :type="scope.row.app_type==='mlExp' ? 'primary' : 'success'">{{scope.row.app_type==='mlExp'?'模型':'普通'}}&nbsp;</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="result" label="创建时间" min-width="19%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <span style="font-size: 16px;">{{moment(scope.row.updateTime).format("YYYY-DD-MM HH:mm:ss")}}</span>
                </template>
            </el-table-column>

            <el-table-column prop="description" label="实验描述" min-width="21%" sortable>
                <template slot-scope="scope">
                    <span style="font-size: 16px;">{{scope.row.description}}</span>
                </template>
            </el-table-column>

            <el-table-column label="操作" min-width="20%" align='right'>
                <template slot-scope="scope">
                    <el-button type="info" style="text-align:right" size="small" @click="LookExpDetails(scope.$index, scope.row)">查看</el-button>
                    <el-button type="danger" style="text-align:right" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
            </el-pagination>
        </el-col>

    </section>
</template>

<script>
import { test } from '../../../api/api'
import $ from 'jquery'
import moment from 'moment'

export default {
  data () {
    return {
      firstID: 'all',
      altListData: [],
      moment: moment,
      project: '',
      case: '',
      ApiList: [],
      listLoading: false,
      searchName: '',
      total: 0,
      page: 1,
      sels: [], // 列表选中列
      activeIndex: ''
    }
  },
  methods: {
    LookExpDetails (index, row) {
      let self = this
      self.$router.push({ name: '查看实验详情',
        params: { api_id: row.id } })
    },
    expandExpClick (val) {
      this.getExpAltList(val.id)
      // console.log("expandExpClick",this.altListData,val.id)
    },
    getCaseApiList () {
      this.listLoading = true
      let self = this
      console.log(self.page, this.$route.params.case_id, 'datadatadata')
      $.ajax({
        type: 'get',
        url: test + '/api/automation/case_api_list',
        async: true,
        data: { project_id: this.$route.params.project_id,
          page: self.page,
          name: self.searchName,
          case_id: this.$route.params.case_id
        },

        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.listLoading = false
          if (data.code === '999999') {
            self.ApiList = []
            self.total = data.data.total
            data.data.data.forEach((item) => {
              item.result = false
              self.ApiList.push(item)
            })
            // self.ApiList = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },

    handleDel (index, row) {
      this.$confirm('确认删除该实验吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        $.ajax({
          type: 'post',
          url: test + '/api/automation/del_abcase',
          async: true,
          data: JSON.stringify({
            project_id: Number(this.$route.params.project_id),
            case_id: Number(this.$route.params.case_id),
            api_id: Number(row.id),
            user_key: JSON.parse(sessionStorage.getItem('name')) }),

          headers: {
            'Content-Type': 'application/json',
            Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          },
          timeout: 5000,
          success: function (data) {
            if (data.code === '999999') {
              self.$message({
                message: '删除成功',
                center: true,
                type: 'success'
              })
            } else {
              self.$message.error({
                message: data.msg,
                center: true
              })
            }
            self.getCaseApiList()
          }
        })
      }).catch(() => {
      })
    },
    // 获取实验候选
    getExpAltList (expId) {
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/experiment/case_exp_altlist',
        async: true,
        data: { project_id: this.$route.params.project_id,
          exp_id: expId },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.altListData = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getCaseApiList()
    }

  },
  mounted () {
    this.getCaseApiList()
    this.project = this.$route.params.project_id
    this.case = this.$route.params.case_id
    if (this.$route.params.firstID) {
      this.firstID = this.$route.params.firstID
    } else {
      this.firstID = 'all'
    }

    // this.firstID = this.$route.params.firstGroup
    // console.log(this.firstID, 'this.$route.params.project_id')
  }
}
</script>

<style lang="scss" scoped>
    p{
        margin: 0 0 10px;
    }
    .return-list {
        margin-top: 0px;
        margin-bottom: 10px;
        border-radius: 25px;
    }
    .HttpStatus {
        border-radius: 25px;
        padding: 10px;
        box-sizing: border-box;
        color: #fff;
        font-size: 15px;
        background-color: #409eff;
        text-align: center;
        margin-right: 10px;
    }
    .resultStyle{
        margin-left: 2%;
        margin-top: 10px;
    }
    .lin{
        left: 2%;
        border: 1px solid #e6e6e6;
        width: 90%;
        position: relative
    }
</style>
